@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text-wrapper {
  padding: var(--padding);
  min-width: 330px;
  width: 50%;
  margin-top: 10vh;
  /* background-color: yellow; */
}

.text-header {
  margin-top: var(--margin-small);
}

.img-wrapper {
  width: 30vw;
  margin-top: 5vh;
}

@media (max-width: 600px) {
  .img-wrapper {
    width: 75vw;
    margin-top: 2vh;
  }

  .text-wrapper {
    padding: 0;
  }

  .text-header {
    margin: 0;
  }
}

.experience__wrapper {
  padding: var(--padding);
  min-width: 330px;
  width: 95%;
  /* border: 2px solid yellow; */
}

.experience__header {
  margin-top: var(--margin-small);
  /* border: 2px solid orange; */
}

.experience__cards-wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
}

.experience__card {
  margin: 30px 30px 30px 0;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: var(--padding);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.experience__card-img:hover {
  height: 200px;
}

.experience__card-label {
}

.experience__card-description {
  height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.experience__card-description p {
  font-style: italic;
}

.experience__card-img {
  /* border: 3px solid green; */
  height: 200px;
}

.experience__btn-wrapper {
  display: -webkit-flex;
  display: flex;
}

@media (max-width: 600px) {
  .experience__img-wrapper {
    width: 75vw;
  }

  .experience__cards-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
}

.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  /* padding: var(--side-padding); */
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
  height: 100px;
  align-items: center;
}

.logo-wrapper {
  width: 150px;
  margin: 0 0 0 var(--margin-medium);
}

.logo {
  width: 100%;
}

.tabs-wrapper {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  margin: 0 var(--margin-small) 0 0;
  font-weight: bold;
  -webkit-align-items: center;
          align-items: center;
}

.tab {
  display: -webkit-flex;
  display: flex;
  text-align: center;
}

.active-tab {
  background-color: var(--primary-yellow);
  /* border-radius: 30px 30px 0 0; */
  /* box-shadow: 12px 12px 2px 1px rgb(var(--primary-yellow)); */
}

@media (max-width: 600px) {
  .logo-wrapper {
    display: none;
  }

  .navbar {
    padding: 0;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .tabs-wrapper {
    margin: 0;
  }
}

.tab {
  text-decoration: none;
  padding: var(--padding);
  cursor: pointer;
  text-align: center;
  border-radius: 10px;
}

.tab:hover {
  background: var(--primary-yellow__hover);
  border-radius: 10px;

  /* border-radius: 30px 30px 0 0; */
}

.btn {
  border: 1px solid var(--primary-yellow);
  cursor: pointer;
  padding: var(--padding);
  margin: var(--margin-small);
  background-color: var(--primary-yellow);
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
}

.btn:hover {
  background: var(--primary-yellow__hover);
  border-radius: 10px;
}

.btn-wrapper {
  margin: var(--margin-medium);
}

.icon-wrapper {
  margin-top: 25vh;
  width: 50vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  /* background-color: yellow; */
}

.icon {
  width: 80%;
  border: 2px solid var(--primary-yellow);
  /* border-radius: 30px; */
  padding: var(--padding);
}

@media (max-width: 600px) {
}

:root {
  --primary-yellow: rgb(240, 183, 15);
  --primary-yellow__hover: rgba(240, 183, 15, 0.4);
  --text-color: rgb(58, 62, 70);
  --font-size: 1.03em;
  --font-size__responsive: 0.8em;
  --header-1: 1.5em;
  --header-2: 1.3em;
  --header-3: 1.1em;
  --font-size-header-responsive: 1.2em;
  --title-medium: 1.5em;
  --title-large: 2em;
  --margin-small: 1em;
  --margin-medium: 2em;
  --margin-large: 3em;
  --padding: 1em;
  --side-padding: 1rem calc((100vw - 1700px) / 2);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Noto Sans JP, sans-serif;
  color: rgb(58, 62, 70);
  color: var(--text-color);
  font-size: 1.03em;
  font-size: var(--font-size);
}

html {
  background: rgb(255, 255, 255);
}

.img {
  width: 100%;
  border-radius: 200px;
  padding: 1em;
  padding: var(--padding);
}

.content-wrapper {
  margin: auto;
  margin-top: 5vh;
  padding: 1rem calc((100vw - 1700px) / 2);
  padding: var(--side-padding);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  text-align: center;
  width: 95vw;
  -webkit-align-items: center;
          align-items: center;
  /* border: 1px solid greenyellow; */
}

.title-large {
  font-size: 2em;
  font-size: var(--title-large);
  /* margin: 0 var(--margin-small) 0 var(--margin-small); */
  padding: 1em;
  padding: var(--padding);
}

.title-medium {
  font-size: 1.5em;
  font-size: var(--title-medium);
}

.card-content-wrapper {
  display: -webkit-flex;
  display: flex;
  border: 1px solid blue;
}

.card-text-wrapper {
  border: 1px solid green;
  margin: 1em;
  margin: var(--padding);
}

.card-img-wrapper {
  width: 35%;
  /* border: 3px solid silver; */
}

.card-img {
  width: 100%;
  border-radius: 10px;
  padding: 1em;
  padding: var(--padding);
}

.card {
  margin: 1em 0 1em 0;
  margin: var(--margin-small) 0 var(--margin-small) 0;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  padding: 1em;
  padding: var(--padding);
  /* border: 2px solid black; */
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.card-text-header {
  margin: 10px;
  text-align: left;
}

.card-text-body {
  list-style: circle;
  text-align: left;
}

.header-1 {
  font-size: 1.5em;
  font-size: var(--header-1);
  font-weight: bold;
}

.header-2 {
  font-size: 1.3em;
  font-size: var(--header-2);
  font-weight: bold;
}

.header-3 {
  font-size: 1.1em;
  font-size: var(--header-3);
  font-weight: bold;
}

.link {
  text-decoration: underline;
  color: rgb(240, 183, 15);
  color: var(--primary-yellow);
}

@media (max-width: 600px) {
  :root {
    font-size: 0.8em;
    font-size: var(--font-size__responsive);
  }
  .content-wrapper {
    margin-top: 2em;
    margin-top: var(--margin-medium);
    padding: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .title-large {
    padding: 0.2em;
  }

  .card-content-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .card-img-wrapper {
    width: 100%;
  }
}

